import './App.css';
import {useState} from "react";
import Axios from 'axios'

function App() {

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [review, setReview] = useState('');

  const [reviews, setReviews] = useState([]);
  //* Sending to BE
  const sendReview = () => {
    Axios.post('http://localhost:3001/create', {name: name, email: email, review: review}).then(()=> {console.log()})
  };

  //* Getting data back from db/BE
  const getReviews = () => {
      Axios.get('http://localhost:3001/reviews', {name: name, email: email, review: review}).then((response)=> {setReviews(response.data)})
  }

  return (
    <div className="App">
      <div className="info">
        <label>Name: </label>
        <input type="text" 
          onChange={(e) =>{
            setName(e.target.value)}}>
          </input>
          
        <label>Email: </label>
        <input type="text"
          onChange={(e) => {
            setEmail(e.target.value)
        }}>
        </input>
        <label>Review: </label>
        <input id="review" type="text"
          onChange={(e) => {
            setReview(e.target.value)
        }}>
        </input>
        <button onClick={sendReview}>Send!</button>
      </div>
      <button className="btn" onClick={getReviews}>Show Reviews</button>
      {reviews.map((rev, key) => {
        return(
          <div className="revMap"key={rev.id}>
              <h4>{rev.name}</h4>
              <h3>{rev.email}</h3>
              <h3>{rev.review}</h3>
          </div>
        )
      })}
    </div>
  );
}

export default App;
